	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Straight Up, Now Tell Me","date":"19 Feb 2001","slug":"/2001-02-19-straight-up-now-tell-me/","tags":"Y2001"},"html":"<p>Astute readers will have noticed by now that I often give titles to my pages that have nothing to do with the content. <a href=\"http://www.vfth.com\">Al</a> names his pages after song titles, but he at least gives the lyrics. I don't even bother to do that. My title yesterday was a current German pop song sung by TV actresses. Noone outside of Germany would recognize it, and since German webloggers don't watch TV and only listen to <a href=\"http://www.sexyjazz.de\">sexyjazz</a>, no German reader would have recognized it either.</p>"},"next":{"fields":{"title":"Daylight In Your Eyes","date":"18 Feb 2001","slug":"/2001-02-18-daylight-in-your-eyes/"}},"previous":{"fields":{"title":"With or Without You","date":"20 Feb 2001","slug":"/2001-02-20-with-or-without-you/"}}},{"node":{"fields":{"title":"Daylight In Your Eyes","date":"18 Feb 2001","slug":"/2001-02-18-daylight-in-your-eyes/","tags":"Y2001"},"html":"<p>Update: I finally linked to the <a href=\"http://shanson.editthispage.com/stories/storyReader$501\">code</a>. For now my favorites box is just alphabetical.</p>\n<p>For a long time I've been intending to update the favorites box. I'm too lazy to update it by hand, so I wanted it to automatically copy (via XML) the favorites I choose on Weblogs.Com. Actually, I was so lazy that I was waiting for someone else to figure how to do this. But since noone has bothered to solve my problem yet, <a href=\"http://shanson.editthispage.com/stories/storyReader$501\">I took some time yesterday to start writing some perl</a>. The hard part is done, hopefully today I'll be able to add the finished result to this page.</p>"},"next":{"fields":{"title":"What A Difference A Day Makes","date":"16 Feb 2001","slug":"/2001-02-16-what-a-difference-a-day-makes/"}},"previous":{"fields":{"title":"Straight Up, Now Tell Me","date":"19 Feb 2001","slug":"/2001-02-19-straight-up-now-tell-me/"}}},{"node":{"fields":{"title":"What A Difference A Day Makes","date":"16 Feb 2001","slug":"/2001-02-16-what-a-difference-a-day-makes/","tags":"Y2001"},"html":"<p><b>Late</b><br />\nI slept 3 hours this afternoon. I had to pick up Christopher after all, and nearly overslept. We need to think of a late-afternoon nutrition plan for him. He comes home from day care hungry (yes, they do feed him!), but a whole banana and an entire yoghurt at 5:30pm tend to ruin his supper.</p>\n<p>\n<b>Early</b><br />\nThis morning my sore throat is gone and my eyes are less pink. I'm staying home anyway. I don't even have to worry about picking up Christopher. After 3 days of late meetings, Mama's going to take a short day today.</p>"},"next":{"fields":{"title":"Back to Bed","date":"15 Feb 2001","slug":"/2001-02-15-back-to-bed/"}},"previous":{"fields":{"title":"Daylight In Your Eyes","date":"18 Feb 2001","slug":"/2001-02-18-daylight-in-your-eyes/"}}}],"pathPrefix":"","first":false,"last":false,"index":1019,"pageCount":1101}}

